<template>
  <div class="Article">
    <van-nav-bar title="专栏详情" left-arrow @click-left="onClickLeft" />

    <ul>
      <li>
        <b>{{ Article.title }}</b>
        <img :src="Article.pic" alt class="oo" />
        <div class="html" v-html="Article.content"></div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      Article: {}
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  },
  async created() {
    console.log("get传值", this.$route.query);
    const { data } = await this.axios.get(
      "https://api.it120.cc/small4/cms/news/detail?id=" + this.$route.query.id
    );
    this.Article = data.data;
    console.log(this.Article);
  }
  //   created() {
  //     this.axios
  //       .get(
  //         "https://api.it120.cc/small4/cms/news/detail?id=" + this.$route.query.id
  //       )
  //       .then(msg => {
  //         console.log(msg);
  //         this.Article = msg.data.data
  //       });
  //   }
};
</script>
<style lang="less">
.Article {
  ul {
    li {
      b {
        font-size: 0.2rem;
        color: #333333;
        width: 100%;
        text-align: center;
        margin-top: 0.45rem;
        display: block;
        margin-bottom: 0.2rem;
      }
      img {
        width: 3.78rem;
        height: 2.13rem;
        display: block;
        margin: 0 auto;
        padding: 0rem 0.06rem 0rem 0.06rem;
        border-radius: 0.08rem;
      }
      .html {
        p {
          font-size: 0.14rem;
          color: #333333;
          line-height: 0.38rem;
          text-indent: 2em;
          margin-top: 0.1rem;
          padding: 0rem 0.06rem 0rem 0.06rem;
        }
      }
    }
  }
}
</style>


