import Vue from "vue"
import Vuex from "vuex"
import createPersistedState from "vuex-persistedstate"
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    title: "hello",
    cart: []
  },
  getters: {
    nums(state) {
      let G = 0
      state.cart.forEach((item) => {
        G += item.num
      })
      return G
    },
    total(state) {
      let G = 0
      state.cart.forEach((item) => {
        if (item.checked) {
          G += item.num * item.price
        }
      })
      return G
    },
    select(state) {
      var A
      if (state.cart.length > 0) {
        var A = state.cart.every((item) => {
          return item.checked
        })
      }
      return A
    },
    order(state) {
      return state.cart.filter((item) => {
        return item.checked
      })
    }
  },
  mutations: {
    addCart(state, goods) {
      var L = state.cart.findIndex((item) => {
        return item.id == goods.id
      })
      console.log(L)
      if (L == -1) {
        state.cart.push(goods)
      } else {
        let cart = state.cart
        cart[L].num += goods.num
        state.cart = cart
      }
    },
    sel(state, D) {
      let cart = state.cart
      cart[D.index].checked = D.value
      state.cart = cart
    },
    num(state, D) {
      let cart = state.cart
      cart[D.index].num = D.value
      state.cart = cart
    },
    SelectAll(state, D) {
      var cart = state.cart
      cart.forEach((item) => {
        item.checked = D
      })
      state.cart = cart
    },
    del(state) {
      var deletes = state.cart.filter((item) => {
        return !item.checked
      })
      state.cart = deletes
    }
  },
  actions: {

  },
  modules: {

  },
  plugins: [
    createPersistedState({
      storage: window.sessionStorage
    })
  ]
})



