<template>
  <div class="user">
    <div class="users">
      <p>新用户注册</p>
      <p>很高兴您将成为商城的会员（注册只需一步）</p>
      <p class="search-box" ref="searchBoxOfChatRoom">
        <input
          ref="searchOfChatRoom"
          class="chatroom-search"
          type="number"
          placeholder="手机号码"
          v-model="tel"
          @focus="changBackground(1)"
          @blur="changBackground(2)"
        />
        <van-icon name="phone-o" style="margin-right:0.2rem" />
      </p>
      <p>
        <input type="password" placeholder="密码" v-model="pwd" v-if="!flag" />
        <input type="text" placeholder="密码" v-model="pwd" v-if="flag" />
        <van-icon name="closed-eye" style="margin-right:0.2rem" @click="change" v-if="!ico" />
        <van-icon name="eye-o" style="margin-right:0.2rem" @click="changes" v-if="ico" />
      </p>
      <p>
        <input type="password" placeholder="确认密码" v-model="pwds" v-if="!flags" />
        <input type="text" placeholder="确认密码" v-model="pwds" v-if="flags" />
        <van-icon name="closed-eye" style="margin-right:0.2rem" @click="unlimited" v-if="!icos" />
        <van-icon name="eye-o" style="margin-right:0.2rem" @click="unlimiteds" v-if="icos" />
      </p>
      <p>
        <input type="text" placeholder="用户名" />
        <van-icon name="contact" style="margin-right:0.2rem" />
      </p>
      <p>
        <input type="text" v-model="pic" />
        <img :src="Captcha+key" alt @click="upImg" />
      </p>
      <p></p>
      <p>
        <input type="text" v-model="code" />
        <span @click="getCode" :disabled="attcode" v-if="showbtn">获取验证码</span>
        <span v-else>{{code_ts}}</span>
      </p>
      <p @click="registered">立即注册</p>
      <p>
        已有账号？
        <span @click="login">立即登录</span>
      </p>
    </div>
  </div>
</template>
<script>
import { v4 as uuidv4 } from "uuid";
import qs from "qs";

export default {
  data() {
    return {
      Captcha: "https://api.it120.cc/small4/verification/pic/get?key=",
      key: uuidv4(),
      tel: "",
      pic: "",
      pwd: "",
      pwds: "",
      code: "",
      flag: false,
      ico: false,
      flags: false,
      icos: false,
      confirm: true, //提交验证按钮判断
      attcode: true, //点击获取验证码按钮判断
      showbtn: true, // 展示获取验证码或倒计时按钮判断
      yzcode: "",
      code_ts: "获取验证码", //倒计时提示文字
      sec: 60 // 倒计时秒数
    };
  },
  methods: {
    changBackground(flag) {
      switch (flag) {
        case 1:
          console.log("获取焦距");
          this.$refs.searchBoxOfChatRoom.style.background = "white";
          this.$refs.searchOfChatRoom.style.background = "white";
          break;
        case 2:
          console.log("失去焦距");
          this.$refs.searchBoxOfChatRoom.style.background = "rgb(109, 199, 150)";
          this.$refs.searchOfChatRoom.style.background = "rgb(109, 199, 150)";
          this.$notify("请输入正确手机号码");
          break;
        default:
          break;
      }
    },
    login() {
      this.$router.push({
        path: "/Board"
      });
    },
    upImg() {
      this.key = uuidv4();
    },
    getCode() {
      var data = { mobile: this.tel, picCode: this.pic, key: this.key };
      this.axios
        .post(
          "https://api.it120.cc/small4/verification/sms/get",
          qs.stringify(data)
        )
        .then(msg => {
          console.log(msg);
        });
      var timer = setInterval(() => {
        this.sec = this.sec - 1;
        this.code_ts = this.sec + "S后重试";
        this.showbtn = false;
        if (this.sec === 0) {
          clearInterval(timer);
          this.sec = 60;
          this.code_ts = this.sec + "S后重试";
          this.showbtn = true;
        }
      }, 1000);
    },
    // watch: {
    //   tel: function(value) {
    //     var reg = /\b1[3456789]\d{9}\b/g;
    //     let val = reg.test(value);
    //     if (val) {
    //       this.attcode = false;
    //     } else {
    //       this.attcode = true;
    //     }
    //   }
    // },
    registered() {
      var data = {
        mobile: this.tel,
        pwd: this.pwd,
        pwds: this.pwds,
        code: this.code
      };
      this.axios
        .post("https://api.it120.cc/small4/user/m/register", qs.stringify(data))
        .then(msg => {
          console.log(msg);
        });
    },
    change() {
      this.flag = true;
      this.ico = true;
    },
    changes() {
      this.flag = false;
      this.ico = false;
    },
    unlimited() {
      this.flags = true;
      this.icos = true;
    },
    unlimiteds() {
      this.flags = false;
      this.icos = false;
    }
  }
};
</script>
<style lang="less">
.user {
  padding: 0.2rem;
  //   min-height: 1rem;
  .users {
    width: 2.7rem;
    padding: 0 0.44rem;
    min-height: 8rem;
    border: 0.02rem solid #f5f5f5;
    p:nth-of-type(1) {
      font-size: 0.26rem;
      margin-top: 0.4rem;
    }
    p:nth-of-type(2) {
      font-size: 0.16rem;
      color: #909090;
      margin-top: 0.24rem;
      margin-bottom: 0.2rem;
    }
    p:nth-of-type(3) {
      width: 2.7rem;
      height: 0.48rem;
      border-radius: 0.06rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
    }
    p:nth-of-type(4) {
      width: 2.7rem;
