      display: flex;
      -webkit-box-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
    }
    p:nth-of-type(5) {
      width: 2.7rem;
      height: 0.48rem;
      border-radius: 0.06rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
    }
    p:nth-of-type(6) {
      width: 2.7rem;
      height: 0.48rem;
      border-radius: 0.06rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
    }
    p:nth-of-type(7) {
      width: 2.7rem;
      height: 0.48rem;
      border-radius: 0.06rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
    }
    p:nth-of-type(8) {
      width: 2.7rem;
      height: 0.48rem;
      border-radius: 0.06rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
    }
    p:nth-of-type(9) {
      width: 2.7rem;
      height: 0.48rem;
      border-radius: 0.06rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      -webkit-box-align: center;
