      border: 1px solid #f5f5f5;
    }
    p:nth-of-type(10) {
      width: 2.2rem;
      height: 0.44rem;
      border-radius: 0.06rem;
      background: linear-gradient(90deg, #5ea6f8, #bb87f6);
      text-align: center;
      line-height: 0.44rem;
      font-size: 0.2rem;
      color: white;
      letter-spacing: 0.06rem;
      margin: 0 auto;
      margin-top: 0.24rem;
    }
    p:nth-of-type(11) {
      font-size: 0.14rem;
