      color: #000;
    }
    p:nth-of-type(7) img {
      width: 1.6rem;
      height: 0.48rem;
      position: absolute;
      right: 0;
      top: 0;
    }
    p:nth-of-type(9) input {
      margin-left: 0.2rem;
      width: 1.2rem;
      height: 0.4rem;
      border: none;
      outline: none;
      background: #f5f5f5;
      font-size: 0.14rem;
    }
    p:nth-of-type(9) span {
      display: block;
      width: 2.7rem;
      height: 0.48rem;
      border-left: 0.01rem solid #e1e1e1;
      font-size: 0.14rem;
      text-align: center;
      line-height: 0.48rem;
      cursor: pointer;
    }
  }
}
