<template>
  <div>
    <div class="search">
      <input type="text" placeholder="回车搜索商品" />
    </div>

    <div class="boss">
      <ol>
        <li @click="AllCategories" :class="{'active':iNow === 0}">所有分类</li>
        <li
          v-for="(item,index) in ListOnTheLeft"
          :key="item.id"
          @click="ClickList(item.id,index)"
          :class="{'active':iNow === (index+1)}"
        >{{ item.name }}</li>
      </ol>
      <div class="right">
        <div class="rightItem">
          <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item>
              <img
                src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1594620774067&di=05d57862d444c7c19c4c3af2d5e4a812&imgtype=0&src=http%3A%2F%2Fimg1.juimg.com%2F141104%2F330501-141104100i027.jpg"
                alt
              />
            </van-swipe-item>
            <van-swipe-item>
              <img
                src="https://assets.hermes.cn/is/image/hermesedito/P_169_Voitures_Exquises?fit=wrap,0&wid=730"
                alt
              />
            </van-swipe-item>
            <van-swipe-item>
              <img
                src="https://assets.hermes.cn/is/image/hermesedito/P_169_ODVI_HD?fit=wrap,0&wid=730"
                alt
              />
            </van-swipe-item>
            <van-swipe-item>
              <img
                src="https://assets.hermes.cn/is/image/hermesedito/P_169_MOF_HD?fit=wrap,0&wid=730"
                alt
              />
            </van-swipe-item>
          </van-swipe>
        </div>
        <ul>
          <li v-for="(item) in EachItem" :key="item.id" @click="ProductDetails(item.id)">
            <img :src="item.icon" alt />
            <p>{{ item.name }}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      EachItem: [], // 九宫格的每一项
      ListOnTheLeft: [], // 左侧列表
      Jiugongge: [], // 九宫格
      iNow: 0
    };
  },
  created() {
    this.getData();
  },
  methods: {
    async getData() {
      // 所有数据
      const D = await this.axios.get(
        "https://api.it120.cc/small4/shop/goods/category/all"
      );
      console.log(D);
      if (D.data.msg == "success") {
        // 过滤出左侧列表的数据
        var LeftColumn = D.data.data.filter(item => {
          return item.level == 1;
        });
        // console.log(LeftColumn);
        this.ListOnTheLeft = LeftColumn;
        this.Jiugongge = D.data.data; // 把所有数据放到九宫格
        this.EachItem = D.data.data;
      }
    },

    ClickList(id, index) {
      // 过滤一级分类所对应的二级分类数据
      var nine = this.Jiugongge.filter(item => {
        return item.pid == id;
      });
      //   console.log(nine);
      this.EachItem = nine;
      this.iNow = index + 1;
    },

    AllCategories() {
      this.EachItem = this.Jiugongge; // 右侧九宫格显示所有数据
      this.iNow = 0; // 设置激活样式
    },

    ProductDetails(id) {
      this.$router.push({
        path: "/ProductDetails?cid=" + id
      });
    }
  }
};
</script>
<style lang="less">
.active {
  color: rgb(192, 3, 3);
  border-left: 0.02rem solid rgb(192, 3, 3);
}
.search {
  width: 100%;
  height: 0.46rem;
  border-bottom: 0.01rem solid #f3f3f3;
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  input {
    width: 2.6rem;
    height: 0.3rem;
    background: #f5f5f5;
    border: none;
    border-radius: 0.2rem;
    margin-left: 0.16rem;
    padding-left: 0.2rem;
  }
}
.boss {
  display: flex;
  ol {
    width: 1.27rem;
    border-right: 0.01rem solid #e9e9e9;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0.48rem;
    overflow: auto;
    background: white;
    z-index: 1;
    li {
      width: 1.27rem;
      height: 0.4rem;
      font-size: 0.14rem;
      text-align: center;
      line-height: 0.4rem;
      margin-top: 0.1rem;
    }
  }
  .right {
    position: fixed;
    left: 1.27rem;
    top: 0.48rem;
    overflow: auto;
    height: 100%;
    .rightItem {
      width: 3.92rem;
      height: 1.36rem;
      .my-swipe .van-swipe-item {
        color: #fff;
        font-size: 20px;
        line-height: 150px;
        text-align: center;
        img {
          width: 2.1rem;
          height: 1.2rem;
          display: block;
          margin: 0.14rem 0rem 0rem 0.32rem;
          border-radius: 0.04rem;
        }
      }
    }
  }
  ul {
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 2.4rem;
    text-align: center;
    padding: 0 0.1rem;
    li {
      width: 30%;
      margin: 0.1rem 0rem 0.1rem 0rem;
      img {
        width: 0.9rem;
        height: 0.9rem;
        display: block;
      }
      p {
        font-size: 0.12rem;
        color: #3e4561;
        margin-top: 0.04rem;
      }
    }
  }
}
</style>


