<template>
  <div class="div">
    <ul>
      <li v-for="(item,index) in hao" :key="index">
        <img :src="item.pic" alt />
        <b>{{ item.name }}</b>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      hao: []
    };
  },
  async created() {
    const { data } = await this.axios.get(
      "https://api.it120.cc/small4/shop/goods/kanjia/list"
    );
    this.hao = data.data.goodsMap;
    console.log(this.hao);
  }
};
</script>
<style lang="less">
.div {
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    li {
      width: 1.8rem;
      height: 2.3rem;
      margin-top: 0.1rem;
      list-style: none;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      img {
        width: 1.8rem;
        height: 1.9rem;
        border-radius: 0.04rem;
      }
      b {
        font-size: 0.12rem;
      }
    }
  }
}
</style>


