<template>
  <div class="login">
    <div class="logins">
      <p>登陆账号</p>
      <p>虾米-严选商城欢迎您</p>
      <p>
        <input type="text" placeholder="手机号码" v-model="tel" />
        <van-icon name="phone-o" style="margin-right:0.2rem" />
      </p>
      <p>
        <input type="password" placeholder="密码" v-model="pwd" v-if="!flags" />
        <input type="text" placeholder="密码" v-model="pwd" v-if="flags" />
        <van-icon name="closed-eye" style="margin-right:0.2rem" @click="unlimited" v-if="!icos" />
        <van-icon name="eye-o" style="margin-right:0.2rem" @click="unlimiteds" v-if="icos" />
      </p>
      <p @click="login">登陆</p>
      <p>忘记密码</p>
      <p>
        还没有注册？
        <span @click="registered">立即注册</span>
      </p>
    </div>
  </div>
</template>
<script>
import qs from "qs";
export default {
  data() {
    return {
      flag: false,
      ico: false,
      flags: false,
      icos: false,
      tel: "",
      pwd: "",
      verification: ""
    };
  },
  methods: {
    login() {
      var data = {
        mobile: this.tel,
        pwd: this.pwd
      };
      this.axios
        .post(
          "https://api.it120.cc/small4/user/m/login?deviceId=007&deviceName=monkey",
          qs.stringify(data)
        )
        .then(msg => {
          console.log(msg);
          if (msg.data.msg == "success") {
            localStorage.setItem("token", msg.data.data.token);
            if (this.verification != "") {
              this.$router.push(this.verification);
            } else {
              this.$router.push("/home/ShouYe");
            }
          } else {
            this.$notify("请输入正确手机号码或密码");
          }
        });
    },
    registered() {
      this.$router.push({
        path: "/NewRegistration"
      });
    },
    change() {
      this.flag = true;
      this.ico = true;
    },
    changes() {
      this.flag = false;
      this.ico = false;
    },
    unlimited() {
      this.flags = true;
      this.icos = true;
    },
    unlimiteds() {
      this.flags = false;
      this.icos = false;
    }
  },
  created() {
    console.log(this.$route);
  },
  beforeRouteEnter(to, from, next) {
    console.log(from);
    next(vm => {
      vm.verification = from.path;
    });
  }
};
</script>
<style lang="less">
.login {
  padding: 0.2rem;

  .logins {
    width: 2.7rem;
    padding: 0 0.44rem;
    min-height: 6.4rem;
    border: 0.02rem solid #f5f5f5;
    p:nth-of-type(1) {
      font-size: 0.26rem;
      margin-top: 0.4rem;
    }
    p:nth-of-type(2) {
      font-size: 0.16rem;
      color: #909090;
      margin-top: 0.24rem;
      margin-bottom: 0.2rem;
    }
    p:nth-of-type(3) {
      width: 2.7rem;
      height: 0.48rem;
      border-radius: 0.06rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
    }
    p:nth-of-type(4) {
      width: 2.7rem;
      height: 0.48rem;
      border-radius: 0.06rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
    }
    p:nth-of-type(5) {
      width: 2rem;
      height: 0.48rem;
      border-radius: 0.06rem;
      background: linear-gradient(135deg, #e570e7 0%, #79f1fc 100%);
      text-align: center;
      line-height: 0.48rem;
      font-size: 0.16rem;
      color: white;
      letter-spacing: 0.06rem;
      margin: 0 auto;
      margin-top: 0.24rem;
    }
    p:nth-of-type(6) {
      font-size: 0.12rem;
      color: #989898;
      width: 100%;
      text-align: center;
      margin-top: 0.24rem;
    }
    p:nth-of-type(7) {
      color: #549ff9;
      font-size: 0.14rem;
      width: 100%;
      text-align: center;
      margin-top: 0.24rem;
    }
    p:nth-of-type(7) span {
      color: #549ff9;
      font-size: 0.14rem;
      width: 100%;
      text-align: center;
      margin-top: 0.24rem;
    }
    p:nth-of-type(3) input {
      margin-left: 0.2rem;
      width: 2.6rem;
      height: 0.4rem;
      border: none;
      outline: none;
      background: #f5f5f5;
      font-size: 0.14rem;
      color: #bfbfbf;
    }
    p:nth-of-type(4) input {
      margin-left: 0.2rem;
      width: 2.6rem;
      height: 0.4rem;
      border: none;
      outline: none;
      background: #f5f5f5;
      font-size: 0.14rem;
      color: #bfbfbf;
    }
  }
}
</style>


