<template>
  <div>
    <li @click="UpPopularRecommendation(good.id)">
      <img :src="good.pic" alt />
      <p>{{ good.name }}</p>
      <p>{{ good.characteristic }}</p>
      <p>￥{{ good.pingtuanPrice }}</p>
    </li>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  props: ["good"],
  methods: {
    UpPopularRecommendation(id) {
      this.$router.push({
        name: "Details",
        params: { id }
      });
    }
  }
};
</script>
<style lang="less">
</style>


