<template>
  <div class="BargainingForAll">
    <b>
      全民砍价
      <van-icon name="arrow" />
    </b>
    <ul>
      <ItemBargainingForAll  v-for="(item,index) in BargainingForAll" :key="index" :good="item"/>
    </ul>
  </div>
</template>
<script>
import ItemBargainingForAll from "@/components/ItemBargainingForAll";
export default {
  components: {
    ItemBargainingForAll
  },
  data() {
    return {
      BargainingForAll: []
    };
  },
  async created() {
    const { data } = await this.axios.get(
      "https://api.it120.cc/small4/shop/goods/kanjia/list"
    );
    this.BargainingForAll = data.data.goodsMap;
    console.log(this.BargainingForAll);
  }
};
</script>
<style lang="less">
.BargainingForAll {
  b {
    display: block;
    text-align: center;
    height: 0.5rem;
    line-height: 0.5rem;
    border-bottom: 0.01rem solid #e9e9e9;
  }
  ul {
    li {
      .one {
        display: flex;
        padding: 0.1rem;
        border-bottom: 0.01rem solid #e9e9e9;
        img {
          width: 1.28rem;
          height: 1.28rem;
          border-radius: 0.06rem;
          display: block;
        }
        .two {
          margin-left: 0.14rem;
          .yi {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            font-size: 0.16rem;
          }
          .er {
            color: #b2b2b2;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            margin-top: 0.1rem;
          }
          ol {
            display: flex;
            -webkit-box-align: center;
            align-items: center;
            li {
              text-align: center;
              margin-left: 0.5rem;
              margin-top: 0.46rem;
            }
            li:nth-of-type(1) {
              margin-left: 0;
              p:nth-of-type(1) {
                color: #d00000;
                font-weight: 700;
              }
              p:nth-of-type(2) {
                color: #000000;
              }
            }
          }
        }
      }
    }
  }
}
</style>


