<template>
  <div class="PopularRecommendation">
    <b>
      人气推荐
      <van-icon name="arrow" />
    </b>
    <ul>
      <ItemPopularRecommendation
        v-for="(item) in PopularRecommendation"
        :key="item.id"
        :good="item"
      />
    </ul>
  </div>
</template>
<script>
import ItemPopularRecommendation from "@/components/ItemPopularRecommendation.vue";
export default {
  components: {
    ItemPopularRecommendation
  },
  data() {
    return {
      PopularRecommendation: []
    };
  },
  async created() {
    const { data } = await this.axios.get(
      "https://api.it120.cc/small4/shop/goods/list"
    );
    this.PopularRecommendation = data.data;
    console.log(this.PopularRecommendation);
  }
};
</script>
<style lang="less">
.PopularRecommendation {
  b {
    display: block;
    text-align: center;
    height: 0.5rem;
    line-height: 0.5rem;
    border-top: 0.13rem solid #e9e9e9;
  }
  ul {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    border-bottom: 0.13rem solid #e9e9e9;

    li {
      width: 1.8rem;
      height: 2.97rem;
      margin-bottom: 0.04rem;
      img {
        width: 1.8rem;
        height: 2.37rem;
        display: block;
        border-radius: 0.04rem;
      }
      p:nth-of-type(1) {
        font-size: 0.14rem;
        color: #000000;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      p:nth-of-type(2) {
        font-size: 0.12rem;
        color: #b2b2b2;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-top: 0.05rem;
      }
      p:nth-of-type(3) {
        font-size: 0.12rem;
        color: #d00000;
        margin-top: 0.05rem;
      }
    }
  }
}
</style>


