<template>
  <div>
    <li>
      <div class="one">
        <img :src="good.pic" alt />
        <div class="two">
          <p class="yi">{{ good.name }}</p>
          <p class="er">{{ good.characteristic }}</p>
          <ol>
            <li>
              <p>{{ good.minPrice }}</p>
              <p>低价</p>
            </li>
            <li style="color:#ccc">
              <p>{{ good.originalPrice }}</p>
              <p>原价</p>
            </li>
            <li style="color:#ccc">
              <p>{{ good.stores }}</p>
              <p>限量</p>
            </li>
          </ol>
        </div>
      </div>
    </li>
  </div>
</template>
<script>
export default {
    props:["good"]
};
</script>
<style lang="less">
</style>


