<template>
  <div class="GouWu">
    <van-nav-bar title="购物车" :left-text="text" @click-left="click" />
    <!-- <img
      src="https://assets.hermes.cn/is/image/hermesproduct/035720H860_wornsquare_1?a=a&size=3000,3000&extend=0,0,0,0&align=0,1&$product_item_grid_b$=&resMode=&wid=400&hei=400"
      alt
    />-->

    <!-- 渲染开始 -->
    <div class="HaoBoss">
      <ul>
        <li v-for="(item,index) in list" :key="item.id">
          <div class="bossChild">
            <div class="show">
              <van-checkbox
                v-model="item.checked"
                checked-color="#AB2929"
                icon-size="0.18rem"
                @click="sel(index)"
              ></van-checkbox>
            </div>
            <img :src="item.pic" alt />
            <div class="rights">
              <p class="pp">{{ item.name }}</p>
              <div class="ItemDiv">
                <p v-for="(item) in item.pro" :key="item.id">{{ item.name }}</p>
              </div>
              <div class="leftChild">
                <p>￥{{ item.price }}</p>
                <div class="leftSun">
                  <van-stepper v-model="item.num" integer @change="ch(index)" />
                </div>
              </div>
            </div>
          </div>
          <!-- <p></p> -->
        </li>
      </ul>
    </div>
    <!-- 渲染结束 -->

    <p class="p">猜你喜欢</p>
    <div class="footers">
      <ul>
        <li>
          <img
            src="https://res.gucci.cn/resources//2019/10/8/15705168510806082_content_DiaryPrimaryImg_Standard_288x216_1569577503_DiaryPrimaryImg_S01-FS-BEAUTY-PORTRAITS_001_Default.jpg"
            alt
          />
          <p>1</p>
          <p>2</p>
          <p>
            <span>1</span>
            <span>1</span>
          </p>
        </li>
        <li>
          <img
            src="https://res.gucci.cn/resources//2019/10/8/15705168510806082_content_DiaryPrimaryImg_Standard_288x216_1569577503_DiaryPrimaryImg_S01-FS-BEAUTY-PORTRAITS_001_Default.jpg"
            alt
          />
          <p>1</p>
          <p>2</p>
          <p>
            <span>1</span>
            <span>1</span>
          </p>
        </li>
        <li>
          <img
            src="https://res.gucci.cn/resources//2019/10/8/15705168510806082_content_DiaryPrimaryImg_Standard_288x216_1569577503_DiaryPrimaryImg_S01-FS-BEAUTY-PORTRAITS_001_Default.jpg"
            alt
          />
          <p>1</p>
          <p>2</p>
          <p>
            <span>1</span>
            <span>1</span>
          </p>
        </li>
        <li>
          <img
            src="https://res.gucci.cn/resources//2019/10/8/15705168510806082_content_DiaryPrimaryImg_Standard_288x216_1569577503_DiaryPrimaryImg_S01-FS-BEAUTY-PORTRAITS_001_Default.jpg"
            alt
          />
          <p>1</p>
          <p>2</p>
          <p>
            <span>1</span>
            <span>1</span>
          </p>
        </li>
      </ul>
    </div>

    <van-submit-bar v-show="list.length>0">
      <p v-show="flag===0" @click="order">下单</p>
      <p v-show="flag===1" @click="del">删除</p>
      <p>合计：￥{{$store.getters.total}}</p>
      <van-checkbox v-model="SS" checked-color="#AB2929" icon-size="0.18rem" @change="SelectAll">全选</van-checkbox>
    </van-submit-bar>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      text: "编辑",
      flag: 0
    };
  },
  computed: {
    // goods() {
    //   return this.$store.state.cart;
    // }
    ...mapState({
      list: state => state.cart
    }),
    SS: {
      get: function() {
        return this.$store.getters.select;
      },
      set: function(V) {
        this.$store.commit("SelectAll", V);
      }
    }
  },
  methods: {
    del() {
      this.$store.commit("del");
    },
    order() {
      this.$router.push({
        path: "/order"
      });
    },
    SelectAll(V) {
      // console.log(V);
    },
    ch(index) {
      // alert(this.list[index].num)
      this.$store.commit("num", {
        index: index,
        value: this.list[index].num
      });
    },
    sel(index) {
      this.$store.commit("sel", {
        index: index,
        value: this.list[index].checked
      });
    },
    click() {
      if (this.text == "编辑") {
        this.text = "完成";
        this.flag = 1;
      } else {
        this.text = "编辑";
        this.flag = 0;
      }
    }
  }
};
</script>
<style lang="less">
.GouWu {
  .van-submit-bar__bar {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    align-items: center;
    -webkit-box-pack: end;
    -webkit-justify-content: flex-end;
    justify-content: flex-end;
    height: 50px;
    padding: 0 16px;
    font-size: 12px;
    position: fixed;
    bottom: 0.53rem;
    background-color: #fff;
    width: 4rem;
    border-top: 0.01rem solid #f5f5f5;
    .van-checkbox {
      position: fixed;
      left: 0.12rem;
    }
    p:nth-of-type(1) {
      position: fixed;
      font-size: 0.14rem;
      left: 3.4rem;
      color: #fff;
      z-index: 20000;
    }
    p:nth-of-type(2) {
      position: fixed;
      font-size: 0.14rem;
      left: 3.4rem;
      color: #fff;
      z-index: 20000;
    }
    p:nth-of-type(3) {
      position: fixed;
      font-size: 0.12rem;
      left: 1.86rem;
      color: salmon;
    }
  }
  .HaoBoss {
    ul {
      li {
        height: 1rem;
        display: flex;
        -webkit-box-align: center;
        align-items: center;
        padding-left: 0.1rem;
        width: 3.9rem;
        // position: relative;
        transition: all 0.5s;
        .bossChild {
          display: flex;
          .show {
            display: flex;
            -webkit-box-align: center;
            align-items: center;
            overflow: hidden;
            // cursor: pointer;
            user-select: none;
          }
          img {
            width: 0.8rem;
            height: 0.8rem;
            display: block;
            border-radius: 0.04rem;
            margin-left: 0.17rem;
          }
          .rights {
            width: 2.09rem;
            height: 0.7rem;
            margin-left: 0.24rem;
            .pp {
              font-size: 0.14rem;
            }
            .ItemDiv {
              display: flex;
              align-items: center;
              p {
                font-size: 0.12rem;
                margin-top: 0.12rem;
                color: #b2b2b2;
              }
            }
            .leftChild {
              display: flex;
              -webkit-box-align: center;
              align-items: center;
              margin-top: 0.06rem;
              width: 2.4rem;
              -webkit-box-pack: justify;
              justify-content: space-between;
              p {
                font-size: 0.12rem;
                color: #d00000;
              }
            }
          }
        }
      }
    }
  }
  img {
    width: 100%;
  }
  .p {
    text-align: center;
    height: 0.5rem;
    line-height: 0.5rem;
  }
  .footers {
    ul {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      -webkit-box-pack: justify;
      justify-content: space-around;
      li {
        width: 1.8rem;
        margin-bottom: 0.1rem;
        position: relative;
        margin-bottom: 0.3rem;
        img {
          width: 1.8rem;
          height: 2rem;
          display: block;
        }
        p:nth-of-type(1) {
          font-size: 0.12rem;
          width: 1.8rem;
          height: 0.4rem;
          background: #f0edd4;
          color: #b69b72;
          position: absolute;
          left: 0;
          top: 1.8rem;
          line-height: 0.4rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        p:nth-of-type(2) {
          font-size: 0.14rem;
          margin-top: 0.3rem;
          color: #666;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        p:nth-of-type(3) {
          display: flex;
          -webkit-box-pack: justify;
          justify-content: space-between;
          -webkit-box-align: center;
          align-items: center;
          font-size: 0.2rem;
          margin-top: 0.1rem;
        }
        p:nth-of-type(3) span:nth-of-type(1) {
          font-size: 0.14rem;
          color: #d00000;
        }
        p:nth-of-type(3) span:nth-of-type(2) {
          font-size: 0.12rem;
          color: #b2b2b2;
        }
      }
    }
  }
}
</style>


