<template>
  <div class="Details">
    <div class="icon" @click="ToProductDetails">
      <p>
        <van-icon name="arrow-left" />
      </p>
    </div>

    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item) in banner" :key="item.id">
        <img :src="item.pic" alt />
      </van-swipe-item>
    </van-swipe>

    <div class="bodys">
      <p>{{ ProductDetails.name }}</p>
      <p>{{ ProductDetails.characteristic }}</p>
      <div class="ChildBodys">
        <p>
          低价
          <span>￥{{ ProductDetails.minPrice }}</span>
        </p>
        <p>原价￥{{ ProductDetails.originalPrice }}</p>
        <p>库存{{ ProductDetails.stores }}</p>
      </div>
    </div>

    <!-- loading开始 -->
    <div class="loading" v-if="loading">
      <van-loading type="spinner" color="#1989fa" />
    </div>
    <!-- loading结束 -->

    <van-tabs @click="onClick" v-else @change="change">
      <van-tab title="商品介绍">
        <div class="Introduction" v-html="content"></div>
      </van-tab>

      <van-tab title="商品评价">
        <div class="Evaluation">
          <ul>
            <li v-for="item in comments" :key="item.id">
              <van-icon name="user-circle-o" />
              <div class="boss">
                <div class="ping">
                  <p>{{ item.user.nick }}</p>
                  <p>{{ item.goods.goodReputationStr }}</p>
                </div>
                <p class="goodReputationRemark">{{ item.goods.goodReputationRemark }}</p>
                <div class="jia">
                  <p>{{ item.goods.dateAdd }}</p>
                  <p>
                    选择规格:
                    <span>{{ item.goods.property}}</span>
                  </p>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </van-tab>
    </van-tabs>

    <van-goods-action>
      <van-goods-action-icon icon="chat-o" />
      <van-goods-action-icon icon="cart-o" @click="ClickShoppingCart" :badge="$store.getters.nums" />
      <van-goods-action-icon icon="star-o" />
      <van-goods-action-button type="danger" is-link @click="showPopup" text="加入购物车" />
      <van-goods-action-button type="warning" is-link @click="showShopping" text="立即购买" />
    </van-goods-action>

    <div class="big">
      <van-popup v-model="show" position="bottom" closeable>
        <div class="one">
          <img :src="buy.pic" alt />
          <div class="two">
            <p>{{ buy.name }}</p>
            <p>￥{{ buy.minPrice }}</p>
          </div>
        </div>
        <div class="three" v-for="(item,index1) in msgs" :key="item.id">
          <p>{{ item.name }}</p>
          <div
            class="four"
            v-for="(items,index2) in item.childsCurGoods "
            :key="items.id"
            :class="{'active':items.checked}"
            @click="select(index1,index2)"
          >{{items.name}}</div>
        </div>
        <div class="value">
          <b>购买数量</b>
          <van-stepper v-model="value" integer />
        </div>
        <div class="BuyNow" @click="Join">加入购物车</div>
      </van-popup>

      <van-popup v-model="shows" position="bottom" :style="{ height: '56%' }" closeable>
        <div class="AddToShoppingCart">立即购买</div>
      </van-popup>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value: 1,
      closeable: true,
      show: false,
      shows: false,
      loading: true,
      comments: [],
      banner: [],
      ProductDetails: [],
      content: [],
      buy: [],
      msgs: [],
      detail: []
    };
  },
  methods: {
    ClickShoppingCart() {
      this.$router.push({
        path: "/home/GouWu"
      });
    },
    Join() {
      let T = localStorage.getItem("token");
      if (T) {
        let Haomsgs = [];
        this.msgs.forEach(item => {
          let R = item.childsCurGoods.filter(Y => {
            return Y.checked;
          });
          Haomsgs = Haomsgs.concat(R);
        });
        console.log(Haomsgs);
        if (Haomsgs.length < this.msgs.length) {
          this.$notify("请选择商品规格");
          this.show = false;
        } else {
          this.$toast({
            message: "加入成功",
            icon: "success"
          });
          this.show = false;
        }
        let obj = {
          pic: this.detail.basicInfo.pic,
          name: this.detail.basicInfo.name,
          price: this.detail.basicInfo.minPrice,
          num: this.value,
          pro: Haomsgs,
          checked: false,
          id: this.detail.basicInfo.id
        };
        this.$store.commit("addCart", obj);
      } else {
        this.$router.push({
          path: "/Board"
        });
        this.$toast({
          message: "请先登录",
          icon: "fail"
        });
      }
    },
    showShopping() {
      this.shows = true;
    },
    showPopup() {
      this.show = true;
    },
    ToProductDetails() {
      this.$router.go(-1);
    },
    onClick() {},
    change(C) {
      //   console.log(C);
      if (C == 1) {
        if (this.comments.length == 0) {
          this.axios
            .get(
              "https://api.it120.cc/small4/shop/goods/reputation?goodsId=" +
                this.$route.params.id
            )
            .then(msg => {
              console.log(msg);
              if (msg.data.msg == "success") {
                this.comments = msg.data.data;
              } else {
                alert("ssss");
              }
            });
        }
      }
    },
    select(n1, n2) {
      this.msgs[n1].childsCurGoods.forEach((item, index) => {
        if (index == n2) {
          item.checked = true;
          this.$set(this.msgs[n1].childsCurGoods, index, item);
        } else {
          item.checked = false;
          this.$set(this.msgs[n1].childsCurGoods, index, item);
        }
      });
    }
  },
  created() {
    var id = this.$route.params.id;
    this.axios
      .get("https://api.it120.cc/small4/shop/goods/detail?id=" + id)
      .then(msg => {
        // console.log(msg);
        this.banner = msg.data.data.pics;
        this.ProductDetails = msg.data.data.basicInfo;
        // console.log(this.ProductDetails);
        // console.log(this.banner);
        this.content = msg.data.data.content;
        this.loading = false;
        this.buy = msg.data.data.basicInfo;
        // this.msgs = msg.data.data.properties;
        // console.log(this.msgs);
        this.detail = msg.data.data;
        // console.log(this.detail);
        var arr = this.detail.properties;
        arr.forEach(item => {
          item.childsCurGoods.forEach(a => {
            a.checked = false;
          });
          this.msgs = arr;
          // console.log(arr);
        });
      });
  }
};
</script>
<style lang="less">
.Details {
  .big {
    .active {
      border: 0.01rem solid #b6272d;
    }
    .value {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 0.6rem;
      border-top: 0.01rem solid #e9e9e9;
      margin-top: 0.04rem;
      margin-bottom: 0.5rem;
      b {
        margin-left: 0.2rem;
        font-size: 0.14rem;
      }
      .van-stepper {
        margin-right: 0.2rem;
        font-size: 0;
        -webkit-user-select: none;
        user-select: none;
      }
    }
    .BuyNow,
    .AddToShoppingCart {
      width: 100%;
      height: 0.5rem;
      background-color: #b7282e;
      text-align: center;
      line-height: 0.5rem;
      font-size: 0.14rem;
      position: fixed;
      bottom: 0rem;
      color: #fff;
    }
    .one {
      height: 1rem;
      border-bottom: 0.01rem solid #e9e9e9;
      display: flex;
      margin-bottom: 0.1rem;
      img {
        width: 0.75rem;
        height: 0.75rem;
        display: block;
        margin-left: 0.2rem;
        margin-top: 0.1rem;
      }
      .two {
        margin-left: 0.16rem;
        width: 3.5rem;
        p:nth-of-type(1) {
          font-size: 0.14rem;
          margin-top: 0.2rem;
        }
        p:nth-of-type(2) {
          font-size: 0.12rem;
          color: #d00000;
          margin-top: 0.2rem;
        }
      }
    }
    .three {
      display: flex;
      flex-wrap: wrap;
      p {
        width: 100%;
        font-size: 0.14rem;
        margin-left: 0.2rem;
      }
      .four {
        padding: 0.1rem 0.2rem;
        border-radius: 0.06rem;
        margin-top: 0.06rem;
        margin-left: 0.1rem;
        margin-bottom: 0.08rem;
      }
    }
  }
  .Evaluation {
    ul {
      li {
        height: 1.3rem;
        padding: 0 0.2rem;
        border-bottom: 0.01rem solid #e9e9e9;
        display: flex;
        .van-icon-user-circle-o::before {
          content: "\F0D3";
          font-size: 0.6rem;
          margin-top: 0.18rem;
        }
        .boss {
          margin-left: 0.14rem;
          .goodReputationRemark {
            font-size: 0.12rem;
            margin-top: 0.1rem;
          }
          .ping {
            display: flex;
            -webkit-box-align: center;
            align-items: center;
            margin-top: 0.18rem;
            p:nth-of-type(1) {
              font-size: 0.14rem;
            }
            p:nth-of-type(2) {
              width: 0.38rem;
              height: 0.26rem;
              border-radius: 0.04rem;
              color: white;
              text-align: center;
              line-height: 0.26rem;
              background: #b7282e;
              font-size: 0.12rem;
              margin-left: 0.2rem;
            }
          }
          .jia {
            display: flex;
            margin-top: 0.2rem;
            p:nth-of-type(1) {
              font-size: 0.12rem;
              color: #b2b2b2;
            }
            p:nth-of-type(2) {
              font-size: 0.12rem;
              color: #b2b2b2;
            }
          }
        }
      }
    }
  }
  .Introduction {
    li {
      height: 0.3rem;
      line-height: 0.27rem;
      padding-left: 0.14rem;
      padding-right: 0.24rem;
      margin-top: 0.1rem;
    }
  }
  .loading {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    background: rgba(0, 0, 0, 0.5);
  }
  .img-lazyloaded {
    width: 100% !important;
    height: 1.3rem !important;
  }
  .icon {
    width: 0.26rem;
    height: 0.26rem;
    padding: 0.03rem;
    border-radius: 50%;
    border: 0.02rem solid #ffffff;
    position: fixed;
    left: 0.1rem;
    top: 0.1rem;
    text-align: center;
    line-height: 0.36rem;
    z-index: 123;
    p {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background: white;
      text-align: center;
    }
    .van-icon-arrow-left::before {
      content: "\F008";
      font-size: 0.2rem;
      font-weight: bold;
      margin-top: 0.03rem;
      color: #bbbbbb;
    }
  }
  .my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    height: 4.5rem;
    img {
      height: 4.5rem;
      width: 4rem;
    }
  }
  .bodys {
    width: 3.8;
    height: 1.2rem;
    background: #ffffff;
    padding-left: 0.2rem;
    border-bottom: 0.1rem solid #f5f5f5;
    p:nth-of-type(1) {
      font-size: 0.2rem;
      color: #464646;
      margin-top: 0.16rem;
    }
    p:nth-of-type(2) {
      font-size: 0.12rem;
      color: #b8b8b8;
      margin-top: 0.14rem;
    }
    .ChildBodys {
      display: flex;
      margin-top: 0.1rem;
      p:nth-of-type(1) {
        font-size: 0.12rem;
        color: #cc0c0c;
        margin-top: 0.06rem;
        width: 0.9rem;
      }
      p:nth-of-type(1) span {
        font-size: 0.14rem;
      }
      p:nth-of-type(2) {
        font-size: 0.12rem;
        color: #b8b8b8;
        margin-top: 0.06rem;
        width: 2.4rem;
      }
      p:nth-of-type(3) {
        font-size: 0.12rem;
        color: #b8b8b8;
        margin-top: 0.06rem;
      }
    }
  }
}
</style>


