<template>
  <div class="FeaturedTopics">
    <b>
      精选专题
      <van-icon name="arrow" />
    </b>

    <!-- loading开始 -->
    <div class="loading" v-if="loading">
      <van-loading type="spinner" color="#1989fa" />
    </div>
    <!-- loading结束 -->

    <div class="scroll" v-else>
      <ul>
        <ItemFeaturedTopics v-for="(item) in FeaturedTopics" :key="item.id" :good="item" />
      </ul>
    </div>
  </div>
</template>
<script>
import ItemFeaturedTopics from "@/components/ItemFeaturedTopics.vue";
export default {
  components: {
    ItemFeaturedTopics
  },
  data() {
    return {
      FeaturedTopics: [],
      loading: true
    };
  },
  async created() {
    const { data } = await this.axios.get(
      "https://api.it120.cc/small4/cms/news/list"
    );
    this.FeaturedTopics = data.data;
    console.log(this.FeaturedTopics);
    this.loading = false;
  }
};
</script>
<style lang="less">
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  background: rgba(0, 0, 0, 0.5);
}
.FeaturedTopics {
  b {
    display: block;
    text-align: center;
    height: 0.5rem;
    line-height: 0.5rem;
    border-top: 0.13rem solid #e9e9e9;
  }
  .scroll {
    width: 100%;
    height: 3rem;
    overflow: auto;
    ul {
      width: 2rem;
      display: flex;
      li {
        width: 3.42rem;
        height: 2.84rem;
        margin-left: 0.12rem;
        img {
          width: 3.42rem;
          height: 2.2rem;
          display: block;
          border-radius: 0.06rem;
        }
        p:nth-of-type(1) {
          font-size: 0.14rem;
          color: #2c2c2c;
          margin-top: 0.12rem;
          width: 4rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        p:nth-of-type(2) {
          font-size: 0.12rem;
          color: #a8a8a8;
          margin-top: 0.1rem;
          width: 3.24rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
  }
}
</style>


