<template>
  <div class="shopping">
    <van-nav-bar title="分类商品" left-arrow :border="false" @click-left="onClickLeft" />
    <ul v-if="list.length != 0">
      <li v-for="(item) in list" :key="item.id" @click="ToDetails(item.id)">
        <img :src="item.pic" alt />
        <p>{{ item.name }}</p>
        <p>￥{{ item.originalPrice }}</p>
      </li>
    </ul>
    <img src="../assets/icon.png" alt class="img" v-else />
  </div>
</template>
<script>
export default {
  data() {
    return {
      border: false,
      list: []
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    ToDetails(id) {
      this.$router.push({
        name:"Details",
        params:{id}
      });
    }
  },
  created() {
    this.axios.get("https://api.it120.cc/small4/shop/goods/list").then(msg => {
      console.log(msg);
      if (msg.data.msg == "success") {
        this.list = msg.data.data.filter(item => {
          return item.categoryId == this.$route.query.cid;
        });
      }
    });
  }
};
</script>
<style lang="less">
.shopping {
  ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    -webkit-box-pack: justify;
    justify-content: space-around;
    li {
      width: 1.8rem;
      margin-bottom: 0.1rem;
      position: relative;
      margin-bottom: 0.3rem;
      img {
        width: 1.8rem;
        height: 2rem;
        display: block;
      }
      p:nth-of-type(1) {
        font-size: 0.12rem;
        width: 1.8rem;
        height: 0.4rem;
        background: #f0edd4;
        color: #b69b72;
        position: absolute;
        left: 0;
        top: 1.8rem;
        line-height: 0.4rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      p:nth-of-type(2) {
        font-size: 0.14rem;
        margin-top: 0.3rem;
        color: #666;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
  .img {
    width: 100%;
    margin-top: 1.4rem;
  }
}
.van-icon-arrow-left::before {
  content: "\F008";
  color: #000;
}
</style>


