<template>
  <div class="order">
    <van-nav-bar title="确认订单" :border="border" left-arrow @click-left="onClickLeft" />
    <!-- <div class="orders">
      <p>+</p>
      <div class="shopping">新增收货地址</div>
      <van-icon name="arrow" />
    </div>-->

    <div class="orders1">
      <van-icon name="location-o" />
      <div class="shopping" @click="GoAddress">
        <p>
          <span>{{ address.linkMan }}</span>
          <span>{{ address.mobile }}</span>
        </p>
        <p>{{ address.address }}</p>
      </div>
      <van-icon name="arrow" />
    </div>

    <img src="../assets/img.png" alt class="img" />
    <p class="class">商品列表</p>
    <ul>
      <li v-for="item in $store.getters.order" :key="item.id">
        <img :src="item.pic" alt />
        <div>
          <p>{{ item.name }}</p>
          <p v-for="items in item.pro" :key="items.id">{{ items.name }}</p>
        </div>
        <div>
          <p>￥{{ item.price }}</p>
          <p>￥{{ item.num }}</p>
        </div>
      </li>
    </ul>
    <div class="delivery">
      <p>配送方式</p>
      <p>快递</p>
    </div>
    <div class="input">
      <p>备注</p>
      <input type="text" placeholder="如需备注请输入" />
    </div>
    <div class="money">
      <p>商品金额</p>
      <p>￥{{ $store.getters.total }}</p>
    </div>
    <van-submit-bar>
      <p>合计：{{ $store.getters.total }}</p>
      <p>提交订单</p>
    </van-submit-bar>
  </div>
</template>
<script>
import qs from "qs";
export default {
  data() {
    return {
      border: false,
      address: {}
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        path: "/home/GouWu"
      });
    },
    GoAddress() {
      this.$router.push({
        path: "/address"
      });
    }
  },
  created() {
    this.axios
      .get(
        "https://api.it120.cc/small4/user/shipping-address/default?token=" +
          localStorage.getItem("token")
      )
      .then(msg => {
        console.log(msg);
        if (msg.data.msg == "success") {
          this.address = msg.data.data;
        }
      });
  }
};
</script>
<style lang="less">
.order {
  .img {
    width: 4rem;
  }
  .van-icon-arrow-left::before {
    content: "\F008";
    color: #000;
  }
  ul li {
    height: 0.86rem;
    border-bottom: 1px solid #e8e8e8;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    img {
      width: 0.64rem;
      height: 0.64rem;
      display: block;
      margin-left: 0.2rem;
    }
    div:nth-of-type(1) {
      margin-left: 0.12rem;
      width: 3.4rem;
      height: 1rem;
      p:nth-of-type(1) {
        font-size: 0.14rem;
        margin-top: 0.19rem;
      }
      p:nth-of-type(2) {
        font-size: 0.12rem;
        margin-top: 0.3rem;
        color: #999;
      }
    }
    div:nth-of-type(2) {
      height: 1rem;
      p:nth-of-type(1) {
        font-size: 0.14rem;
        float: right;
        margin: 0.19rem 0.16rem 0rem 0.14rem;
      }
      p:nth-of-type(2) {
        font-size: 0.12rem;
        float: right;
        margin: 0.3rem 0.16rem 0rem 0.29rem;
        color: #999;
      }
    }
  }
  .van-submit-bar__bar {
    p:nth-of-type(1) {
      left: 1.6rem;
      position: fixed;
      z-index: 2000;
      color: brown;
    }
    p:nth-of-type(2) {
      position: fixed;
      left: 2.95rem;
      color: #fff;
      font-size: 0.16rem;
      z-index: 2000;
    }
  }
  .money {
    height: 0.5rem;
    padding: 0 0.2rem;
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    align-items: center;
    border-top: 0.1rem solid #f5f5f5;
    border-bottom: 0.1rem solid #f5f5f5;
  }
  .input {
    height: 0.59rem;
    padding: 0 0.2rem;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    input {
      width: 2rem;
      height: 0.2rem;
      font-size: 0.12rem;
      color: #333;
      margin-left: 0.76rem;
      border: none;
    }
  }
  .delivery {
    height: 0.6rem;
    padding: 0 0.2rem;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
    font-size: 0.18rem;
    border-bottom: 0.01rem solid #f5f5f5;
    border-top: 0.08rem solid #f5f5f5;
  }
  .class {
    height: 0.5rem;
    padding: 0 0.2rem;
    line-height: 0.5rem;
    font-size: 0.14rem;
    border-top: 0.1rem solid #f5f5f5;
    border-bottom: 2px solid #f5f5f5;
  }
  //   .orders {
  //     height: 0.8rem;
  //     width: 4rem;
  //     display: flex;
  //     -webkit-box-align: center;
  //     align-items: center;
  //     padding: 0 0.2rem;
  //     p:nth-of-type(1) {
  //       width: 0.3rem;
  //       height: 0.3rem;
  //       line-height: 0.3rem;
  //       font-size: 0.2rem;
  //       color: #fff;
  //       font-weight: bold;
  //       border-radius: 0.06rem;
  //       text-align: center;
  //       background: #3283f9;
  //     }
  //     .shopping {
  //       width: 2.85rem;
  //       height: 0.58rem;
  //       line-height: 0.58rem;
  //       margin-left: 0.32rem;
  //     }
  //     .van-icon-arrow::before {
  //       content: "\F00A";
  //       color: #cccc;
  //     }
  //   }
  .orders1 {
    height: 0.8rem;
    width: 3.6rem;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    padding: 0 0.2rem;
    .van-icon-location-o::before {
      content: "\F07C";
      font-size: 0.16rem;
    }
    // .p {
    //   width: 0.3rem;
    //   height: 0.3rem;
    //   line-height: 0.3rem;
    //   font-size: 0.2rem;
    //   color: #fff;
    //   font-weight: bold;
    //   border-radius: 0.06rem;
    //   text-align: center;
    //   background: #3283f9;
    // }
    .shopping {
      width: 2.85rem;
      height: 0.58rem;
      position: relative;
      margin-left: 0.32rem;
      p:nth-of-type(1) {
        margin-top: 0.06rem;
        span:nth-of-type(1) {
          font-size: 0.14rem;
          margin-top: 0.06rem;
        }
        span:nth-of-type(2) {
          font-size: 0.14rem;
          margin-top: 0.06rem;
        }
      }
      p:nth-of-type(2) {
        color: #ccc;
        bottom: 0;
        position: absolute;
        margin-bottom: 0.06rem;
      }
    }
    .van-icon-arrow::before {
      content: "\F00A";
      color: #cccc;
    }
  }
}
</style>


