<template>
  <div>
    <div class="LogInImmediately">
      <p></p>
      <p @click="to">立即登录</p>
      <p>积分:0</p>
      <p @click="SignOut">退出登录</p>
    </div>

    <div class="news">
      <van-notice-bar
        left-icon="volume-o"
        background="#fff"
        text="锡林郭勒凤凰马场，马背上的男人，有故事，感受马文化，策马扬鞭。"
      />
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    to() {
      this.$router.push({
        path: "/Board"
      });
    },
    SignOut() {
      localStorage.removeItem("token");
      this.$router.push({
        path: "/Board"
      });
    }
  }
};
</script>
<style lang="less">
.news {
  padding: 0.04rem 0rem 0.04rem 0rem;
  border-bottom: 0.12rem solid #f5f5f5;
}
.LogInImmediately {
  width: 100%;
  height: 1.7rem;
  display: block;
  background: #c1b18f;
  p:nth-of-type(1) {
    width: 1.06rem;
    height: 1.06rem;
    border-radius: 50%;
    position: absolute;
    left: 0.2rem;
    top: 0.5rem;
    display: flex;
    text-align: center;
    -webkit-box-align: center;
    align-items: center;
  }
  p:nth-of-type(2) {
    font-size: 0.2rem;
    color: white;
    position: absolute;
    left: 1.5rem;
    top: 0.5rem;
  }
  p:nth-of-type(3) {
    width: 1rem;
    padding: 0.1rem;
    color: #fff;
    background: #b3a078;
    border-radius: 0.06rem;
    position: absolute;
    left: 1.5rem;
    top: 0.96rem;
    opacity: 0.8;
    font-size: 0.12rem;
  }
  p:nth-of-type(4) {
    font-size: 0.12rem;
    color: white;
    position: absolute;
    left: 1.5rem;
    top: 1.4rem;
  }
}
</style>


