<template>
  <div class="address">
    <van-nav-bar title="我的地址" left-arrow :border="border" @click-left="onClickLeft">
      <template #right>
        <b @click="New">新建地址</b>
      </template>
    </van-nav-bar>
    <ul>
      <li v-for="item in list" :key="item.id">
        <van-icon name="checked" color="red" />
        <div class="one">
          <div class="two">
            <p>{{ item.linkMan }}</p>
            <p>{{ item.mobile }}</p>
          </div>
          <p class="p">
            {{ item.provinceStr }}
            &nbsp;<span>{{ item.address }}</span>
          </p>
        </div>
        <van-icon name="share" color="#ccc" />
      </li>
    </ul>
  </div>
</template>
<script>
import qs from "qs";
export default {
  data() {
    return {
      border: false,
      list: []
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        path: "/order"
      });
    },
    New() {
      this.$router.push({
        path: "/New"
      });
    }
  },
  created() {
    var D = { token: localStorage.getItem("token") };
    var data = qs.stringify(D);
    this.axios
      .post("https://api.it120.cc/small4/user/shipping-address/list", data)
      .then(msg => {
        console.log(msg);
        if (msg.data.msg == "success") {
          this.list = msg.data.data;
        }
      });
  }
};
</script>
<style lang="less">
.address {
  .van-icon-arrow-left::before {
    content: "\F008";
    color: #000;
  }
  ul {
    li {
      width: 3.8rem;
      height: 0.8rem;
      border-bottom: 1px solid #f5f5f5;
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      padding-left: 0.2rem;
      background: #ffffff;
      position: relative;
      .van-icon-checked::before {
        content: "\F02F";
        font-size: 0.2rem;
      }
      .van-icon-share::before {
        content: "\F0B6";
        font-size: 0.2rem;
        margin-right: 0.2rem;
      }
      .one {
        margin-left: 0.28rem;
        height: 0.58rem;
        width: 3.78rem;
        .p {
          font-size: 0.12rem;
          color: #ccc;
          margin-top: 0.18rem;
          display: flex;
          align-items: center;
        }
        .two {
          display: flex;
          align-items: center;
          p:nth-of-type(1) {
            font-size: 0.12rem;
          }
          p:nth-of-type(2) {
            font-size: 0.12rem;
            margin-left: 0.14rem;
          }
        }
      }
    }
  }
}
</style>


