<template>
  <div class="New">
    <van-nav-bar title="新增地址" :border="border" left-arrow @click-left="onClickLeft" />

    <van-cell-group>
      <van-field v-model="value" label="联系人" placeholder="姓名" />
      <van-field v-model="value" label="手机号码" placeholder="11位手机号" />
      <van-field v-model="value" label="选择地区" placeholder />
      <van-field v-model="value" label="详细地址" placeholder="街道门牌信息" />
      <van-field v-model="value" label="邮政编码" placeholder="邮政编码" />
      <br />
    </van-cell-group>
    <div class="button">
      <van-button type="primary" block>保存</van-button>
      <br />
      <van-button color="linear-gradient(to right, #4bb0ff, #6149f6)" block>从微信读取</van-button>
      <br />
      <van-button color="#ccc" block>取消</van-button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value: "",
      border: false
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        path: "/address"
      });
    }
  }
};
</script>
<style lang="less">
.button {
  width: 3.66rem;
  padding-left: 0.17rem;
  padding-right: 0.17rem;
}
</style>


