<template>
  <div class="BANNER">
    <!-- 轮播图开始 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item,index) in banner" :key="index">
        <img :src="item.picUrl" alt />
      </van-swipe-item>
    </van-swipe>
    <!-- 轮播图结束 -->

    <!-- 轮播图上的导航开始 -->
    <div class="list">
      <ul>
        <li>
          <img src="../assets/cd1.7808faf.png" alt />
          <b>签到</b>
        </li>
        <li>
          <img src="../assets/下载.png" alt />
          <b>礼券</b>
        </li>
        <li>
          <img src="../assets/下载 (1).png" alt />
          <b>砍价</b>
        </li>
        <li>
          <img src="../assets/下载 (2).png" alt />
          <b>专栏</b>
        </li>
      </ul>
    </div>
    <!-- 轮播图上的导航结束 -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      banner: []
    };
  },
  async created() {
    const { data } = await this.axios.get(
      "https://api.it120.cc/small4/banner/list"
    );
    this.banner = data.data;
    console.log(this.banner);
  }
};
</script>
<style lang="less">
.BANNER {
  position: relative;
  .list {
    width: 4rem;
    height: 1.2rem;
    background-color: #fff;
    position: absolute;
    bottom: 0rem;
    border-radius: 0.5rem 0.5rem 0rem 0rem;
    border-bottom: 0.17rem solid #e9e9e9;
    ul {
      height: 1.2rem;
      display: flex;
      align-items: center;
      justify-content: space-around;
      li {
        width: 0.5rem;
        height: 1rem;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        img {
          width: 0.4rem;
          height: 0.4rem;
        }
        b {
          color: #444444;
        }
      }
    }
  }
  .my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    text-align: center;
    img {
      width: 4rem;
    }
  }
}
</style>


