<template>
  <div>
    <li @click="to(good.id)">
      <img :src="good.pic" alt />
      <p>{{ good.title }}</p>
      <p>{{ good.descript }}</p>
    </li>
  </div>
</template>
<script>
export default {
  props: ["good"],
  methods: {
    to(id) {
      this.$router.push("/Article?id=" + id);
    }
  }
};
</script>
<style lang="less">
</style>


