import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Index from "@/pages/index.vue"
import List from "@/pages/list.vue"
import Home from "@/pages/home.vue"
import ShouYe from "@/pages/ShouYe.vue"
import FenLei from "@/pages/FenLei.vue"
import GouWu from "@/pages/GouWu.vue"
import GeRen from "@/pages/GeRen.vue"
import Article from "@/pages/Article.vue"
import Board from "@/pages/Board.vue"
import NewRegistration from "@/pages/NewRegistration.vue"
import ProductDetails from "@/pages/ProductDetails.vue"
import Details from "@/pages/Details.vue"
import order from "@/pages/order.vue"
import address from "@/pages/address.vue"
import New from "@/pages/New.vue"

export default new Router({
  mode: "history",
  routes: [{
      path: "/",
      redirect: "/home/ShouYe"
    },
    {
      path: "/index",
      component: Index
    },
    {
      path: "/list",
      component: List
    },
    {
      path: "/Article",
      component: Article
    },
    {
      path: "/Board",
      component: Board
    },
    {
      path: "/NewRegistration",
      component: NewRegistration
    },
    {
      path: "/ProductDetails",
      component: ProductDetails
    },
    {
      path: "/order",
      component: order
    },
    {
      path: "/address",
      component: address
    },
    {
      path: "/New",
      component: New
    },
    {
      path: "/Details/:id",
      component: Details,
      name: "Details"
    },
    {
      path: "/home",
      component: Home,
      children: [{
          path: "ShouYe",
          component: ShouYe
        },
        {
          path: "FenLei",
          component: FenLei
        },
        {
          path: "GouWu",
          component: GouWu
        },
        {
          path: "GeRen",
          component: GeRen
        },
      ]
    }
  ]
})



